@charset "UTF-8";
//-----------------------------------------------------	
// variable scss
//-----------------------------------------------------	

//font相關
//-----------------------------------------------------	
$baseFontSize:          14px !default; //基本字型
$baseLineHeight:        1 !default; //基本行高
$baseFontFamily:        "Helvetica Neue", Helvetica, Tahoma, sans-serif !default; //基本字型


//元素上下間距
//-----------------------------------------------------	
$baseGap:               20px !default; //上下高度

// 基本顏色
//-----------------------------------------------------	
$main-color:            #87c68d !default; //主色
$vice-color:            #0cacd0 !default; //輔色 

$gray-color1:           #f4f4f4 !default; //灰1
$gray-color2:           #898989 !default; //灰2
$gray-color3:           #252525 !default; //灰3

$wtransparent_1:        hsla(0,100%,100%,.1); //白透1
$wtransparent_3:        hsla(0,100%,100%,.3); //白透3
$wtransparent_5:        hsla(0,100%,100%,.5); //白透5
$wtransparent_7:        hsla(0,100%,100%,.7); //白透7

$btransparent_1:        hsla(0,0%,0%,.1); //黑透1
$btransparent_3:        hsla(0,0%,0%,.3); //黑透3
$btransparent_5:        hsla(0,0%,0%,.5); //黑透5
$btransparent_7:        hsla(0,0%,0%,.7); //黑透7
$btransparent_9:        hsla(0,0%,0%,.9); //黑透9

// radius
//-----------------------------------------------------	
$sRadius:                2px !default; //小圓角
$mRadius:                4px !default; //中圓角
$lRadius:                8px !default; //大圓角
$exRadius:               12px !default; //超大圓角
$circleRadius:           50% !default; //圓型
