@charset "UTF-8";
//-----------------------------------------------------	
// mixin scss
//-----------------------------------------------------	

//自適應尺寸

@mixin large {
    @media(max-width:1200px){
        @content;
    }
}

@mixin big {
    @media(max-width:992px){
        @content;
    }
}

@mixin medium {
    @media(max-width:768px){
        @content;
    }
}

@mixin small {
    @media(max-width:576px){
        @content;
    }
}

//清除浮動
//-----------------------------------------------------
%clearfix::after,%clearfix::before{
    display: block;
    clear: both;
}

//flex對齊
//-----------------------------------------------------
@mixin flex($direction, $arrangePrimary , $arrangeVice){
    display: flex;
    flex-direction: $direction; //row | row-reverse | column | column-reverse;
    justify-content: $arrangePrimary; //flex-start|flex-end|center|space-between|space-around|initial|inherit;
    align-items: $arrangeVice; //flex-start | flex-end | center | baseline | stretch;
}


//方形(寬/高/背景顏色)
//-----------------------------------------------------
@mixin box($width,$height,$bk) {
    width: $width;
    height: $height;
    background: $bk;
}

//圓形(大小/背景顏色)
//-----------------------------------------------------
@mixin circle($size,$bgColor){
    width: $size;
    height: $size;
    background-color: $bgColor;
    border-radius: 50%;
}