// 引用
@import "./core/reset";
@import "./core/variable";
@import "./core/grid";
@import "./core/mixin";
@import "./core/extend";

// ------專案項目------

//------共用內容------
.change-color {
  color: $gray-color3 !important;
}

.nav--wrap__changelogo {
  background: url(../img/logo-black.png) no-repeat;
  width: 180px;
  height: 41px;
  background-size: contain;
}

.moving {
  transform: translate(-50%, -50%) !important;
}

.header {
  width: 100%;
  background: url(../img/header-bk.jpg);
  background-size: cover;

  .nav {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background: $wtransparent_1;
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
    padding: 8px 0;
    z-index: 50;
    transition: all 0.3s;

    &--wrap {
      @extend %wrap;
      @include flex(row, space-between, center);

      &__logo {
        width: 180px;
        height: 41px;
        background: url(../img/logo-white.png) no-repeat;
        background-size: contain;
      }

      &__menu {
        li {
          float: left;

          a {
            display: inline-block;
            color: #fff;
            padding: 0 1rem;
            position: relative;

            &::after {
              content: "";
              display: inline-block;
              position: absolute;
              top: 150%;
              left: 20%;
              width: 20px;
              height: 2px;
              background: $main-color;
              transition: width 0.3s;
            }

            &:hover {
              &::after {
                width: 50px;
              }
            }
          }
        }
      }

      &__sidebar {
        display: none;

        .line {
          width: 24px;
          height: 4px;
          margin-bottom: 4px;
          background: #fff;
        }
      }
    }
  }

  .banner {
    padding-top: 100px;

    &--wrap {
      @extend %wrap;
      padding: 1rem 0;

      &__title {
        font-size: 30px;
        color: #fff;
        margin-bottom: 0.5rem;
      }

      &__narrate {
        font-size: 14px;
        font-weight: 300;
        color: #fff;
        margin-bottom: 2rem;
      }

      a {
        .wantbtn {
          @extend %main-gradient;
          border: none;
          color: #fff;
          font-size: 18px;
          border-radius: $mRadius;
          padding: 0.5rem;
          letter-spacing: 2px;
          margin-bottom: 80px;
          cursor: pointer;

          &:hover {
            filter: brightness(1.1);
          }
        }
      }
    }
  }

  .sidealert {
    width: 90%;
    height: 80%;
    background: linear-gradient(
      45deg,
      rgba(135, 198, 141, 0.9),
      rgba(12, 172, 208, 0.9)
    );
    border-radius: 8px;
    position: fixed;
    top: 55%;
    left: 50%;
    transform: translate(-170%, -50%);
    z-index: 99;
    transition: all 0.2s;

    &--menu {
      padding: 1rem;

      &__item {
        text-align: center;
        color: #fff;
        padding: 1rem;
      }

      a {
        display: block;
      }

      a + a {
        border-top: 1px solid $wtransparent_5;
      }
    }
  }

  @include big {
    .nav--wrap {
      width: 90%;
      margin: 0 auto;
    }

    .nav--wrap__menu {
      display: none;
    }

    .nav--wrap__sidebar {
      display: block;
    }

    .banner--wrap {
      width: 90%;
      margin: 0 auto;
    }
  }
}

.active {
  color: $main-color;
}

.on {
  display: block !important;
}
.form-style {
  color: red;
  font-size: 14px;
  padding-top: 4px;
  margin-left: 12px;
}

.footer {
  background: $gray-color3;

  &--wrap {
    @extend %wrap;
    padding: 2rem 0 1rem;

    &__info {
      @include flex(row, space-between, start);

      .infos {
        width: calc(100% / 3);

        h2 {
          color: $gray-color1 !important;
          font-size: 20px;
          margin-bottom: 1rem;
          border-left: 4px solid $main-color;
          padding-left: 12px;
        }

        p {
          color: $gray-color1;
          margin-bottom: 0.8rem;
          font-weight: 100;
        }
      }

      .third {
        img {
          width: 36px;
          margin-left: 20px;
        }
      }
    }

    &__list {
      text-align: center;
      margin-bottom: 12px;
      font-size: 0;

      li {
        display: inline-block;
        font-size: 1rem;

        a {
          color: $gray-color1;
          font-size: 14px;
          display: inline-block;
          padding: 2px 12px;

          &:hover {
            color: $main-color;
          }
        }
      }

      li + li {
        border-left: 1px solid #fff;
      }
    }

    &__copyright {
      text-align: center;
      color: $gray-color2;
      font-size: 14px;
    }
  }

  @include big {
    .footer--wrap {
      width: 90%;
      margin: 0 auto;
    }

    .infos {
      width: calc(100% / 2);
    }
  }

  @include small {
    .footer--wrap__info {
      @include flex(column, flex-start, start);
    }

    .infos {
      width: 100%;
    }

    .third {
      margin: 12px auto;

      a + a {
        margin-left: 35px;
      }

      img {
        width: 36px;
        margin-left: 0;
      }
    }

    .footer--wrap__list {
      display: none;
    }
  }
}

.owl-carousel .item {
  width: 95%;
  margin: 0 auto;
  position: relative;
  cursor: pointer;

  .hidebox {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
    background: $btransparent_5;
    display: none;
  }
}

//------分頁內容------

//首頁
.about {
  &--wrap {
    @extend %wrap;
    padding: 5rem 0;

    &__title {
      margin-bottom: 42px;

      h2 {
        text-align: center;
        font-size: 28px;
        font-weight: 300;
        color: $gray-color3;
        margin-bottom: 20px;
        position: relative;

        &::after {
          content: "";
          display: inline-block;
          width: 40px;
          height: 4px;
          position: absolute;
          background: $main-color;
          top: 35px;
          left: 50%;
          transform: translateX(-50%);
        }
      }

      h3 {
        text-align: center;
        font-size: 14px;
        font-weight: 300;
        color: $gray-color2;
      }
    }

    &__content {
      width: 60%;
      margin: 0 auto;
      @include flex(row, space-between, start);

      p {
        &:nth-of-type(1) {
          flex: 1;

          em {
            display: block;
            font-weight: 600;
            font-size: 28px;
            color: $main-color;

            &:nth-of-type(2) {
              color: $vice-color;
            }
          }

          em + em {
            padding-top: 1rem;
          }
        }

        &:nth-of-type(2) {
          flex: 2;
          color: $gray-color2;
          line-height: 24px;
        }
      }
    }
  }

  @include big {
    .about--wrap__content {
      width: 85%;
    }
  }
}

