  background: $gray-color1;

  &--wrap {
    @extend %wrap;
    padding: 5rem 0;

    &__title {
      margin-bottom: 42px;

      h2 {
        text-align: center;
        font-size: 28px;
        font-weight: 300;
        color: $gray-color3;
        margin-bottom: 20px;
