        text-align: center;
        font-size: 14px;
        font-weight: 300;
        color: $gray-color2;
      }
    }

    &__content {
      margin: 0 auto;
      display: flex;
      flex-wrap: wrap;

      .advantage {
        display: flex;
        width: calc(100% / 3);

        &--logo {
          width: 48px;
          height: 48px;
        }

