  @include big {
    .features--wrap {
      width: 90%;
      margin: 0 auto;
    }
  }

  @include medium {
    .advantage {
      width: calc(100% / 2);
    }
  }

  @include small {
    .advantage {
      width: calc(100%);
    }
  }
}

