//服務項目

.services {
  &--wrap {
    @extend %wrap;
    padding: 5rem 0;

    &__title {
      margin-bottom: 42px;

      h2 {
        text-align: center;
        font-size: 28px;
        font-weight: 300;
        color: $gray-color3;
        margin-bottom: 20px;
        position: relative;

        &::after {
          content: "";
          display: inline-block;
          width: 40px;
          height: 4px;
          position: absolute;
          background: $main-color;
          top: 35px;
          left: 50%;
          transform: translateX(-50%);
        }
      }

      h3 {
        text-align: center;
        font-size: 14px;
        font-weight: 300;
        color: $gray-color2;
      }
    }

    &__content {
      .table {
        display: flex;

        &--col2 {
          padding: 4px;
          width: 50%;

          &__p {
            border: 1px solid $gray-color1;
            padding: 4px;
            min-height: 205px;
            display: flex;

            div {
              &:nth-of-type(1) {
                width: calc(100% / 3);

                img {
                  display: block;
                  width: 64px;
                  margin: 12px auto;
                }

                h3 {
                  text-align: center;
                  margin-bottom: 4px;
                  color: $main-color;
                }

                h4 {
                  font-weight: 200;
                  font-size: 14px;
                  text-align: center;
                }

                a {
                  color: $main-color;
                  font-weight: 600;
                  font-size: 16px;
                  letter-spacing: 2px;
                  border: 1px solid $main-color;
                  border-radius: 24px;
                  background: transparent;
                  padding: 8px;
                  cursor: pointer;
                  display: block;
                  width: 100%;
                  max-width: 100px;
                  text-align: center;
                  margin: 15% auto 0;

                  &:hover {
                    border-color: $vice-color;
                    color: $vice-color;
                  }
                }
              }

              &:nth-of-type(2) {
                width: calc(100% / 3 * 2);
                padding: 1rem;
                p {
                  line-height: 24px;
                  color: $gray-color3;
                }
              }
            }
          }
        }

        &--col1 {
          padding: 4px;
          width: 25%;

          &__p {
            border: 1px solid $gray-color1;
            margin-bottom: 4px;
            padding: 4px;
            min-height: 100px;

            img {
              display: block;
              width: 32px;
              margin: 12px auto 8px;
            }

            h3 {
              text-align: center;
              margin-bottom: 4px;
              color: $main-color;
            }

            h4 {
              font-weight: 200;
              font-size: 14px;
              text-align: center;
            }
          }
        }
      }
    }
  }

  @include big {
    .services--wrap {
      width: 90%;
      margin: 0 auto;
    }

    .table {
      flex-wrap: wrap;
    }

    .table--col2 {
      width: 100%;
    }

    .table--col1 {
      width: 50%;
    }
  }

  @include medium {
    .table--col2__p div:nth-of-type(2) p {
      word-break: break-all;
    }
  }

  @include small {
    .table--col1 {
      width: 100%;
      padding: 0 !important;
    }

    .table--col2 {
      width: 100%;
      padding: 0;
      margin-bottom: 4px;
    }

    .table--col2__p {
      width: 100%;
      flex-direction: column;
    }

    .table--col2__p div:nth-of-type(1),
    .table--col2__p div:nth-of-type(2) {
      width: 100%;
      padding: 0;
    }

    .table--col2__p div:nth-of-type(1) a {
      margin: 5% auto 0;
    }

    .table--col2__p div:nth-of-type(2) > p {
      text-align: center;
    }
  }
}

.rwd {
