          font-size: 20px;
          color: $gray-color3;
          margin-bottom: 8px;
        }

        p {
          line-height: 24px;
          color: $gray-color2;
          margin-bottom: 12px;
        }

        img {
          width: 70%;
          display: block;
          margin: 0 auto;
        }
      }
    }
  }

  @include big {
    .rwd--wrap {
      width: 90%;
      margin: 0 auto;
    }
  }

  @include medium {
    .rwd--wrap__content {
      flex-direction: column;
    }

    .rwd--wrap__contentinfo {
      width: 100%;
    }
  }
}

