          color: $gray-color3;
          font-weight: 400;
        }

        p {
          padding: 0.5rem 5rem;
          font-size: 14px;
          line-height: 20px;
          color: $gray-color2;
        }
      }
    }
  }

  @include big {
    .reason--wrap {
      width: 90%;
      margin: 0 auto;
    }

    .reasons p {
      padding: 0.5rem 3rem;
    }
  }

  @include medium {
    .reasons {
      width: 50%;
    }

    .reasons p {
      padding: 0.5rem 2rem;
    }
  }

  @include small {
    .reasons {
      width: 100%;
      border-bottom: 1px solid $btransparent_1;
    }

