//設計專區

.design {
  &--wrap {
    @extend %wrap;
    padding: 5rem 0;

    &__title {
      margin-bottom: 42px;

      h2 {
        text-align: center;
        font-size: 28px;
        font-weight: 300;
        color: $gray-color3;
        margin-bottom: 20px;
        position: relative;

        &::after {
          content: "";
          display: inline-block;
          width: 40px;
          height: 4px;
          position: absolute;
          background: $main-color;
          top: 35px;
          left: 50%;
          transform: translateX(-50%);
        }
      }

      h3 {
        text-align: center;
        font-size: 14px;
        font-weight: 300;
        color: $gray-color2;
      }
    }

    &__content {
      &-menu {
        @include flex(row, space-between, center);
        width: 60%;
        margin: 0 auto 1rem;

        li {
          width: 100%;
          text-align: center;
          cursor: pointer;

          &:hover {
            color: $main-color;
          }
        }

        li + li {
          border-left: 1px solid $gray-color2;
        }
      }

      &-project {
        display: none;
        flex-wrap: wrap;

        .works {
          width: calc(100% / 3);
          padding: 1rem;

          &--img {
            cursor: pointer;

            img {
              width: 100%;
              display: block;
            }
          }

          &--title {
            margin-top: 8px;
            text-align: center;
            letter-spacing: 2px;
          }
        }
      }
    }
  }

  @include medium {
    .design--wrap__content-menu {
      width: 80%;
    }

    .works {
      width: 50%;
    }
  }

  @include small {
    .design--wrap__content-menu {
      width: 100%;
    }

    .works {
      width: 100%;
    }
  }
}

