//聯絡我們
.contact {
  &--wrap {
    @extend %wrap;
    padding: 5rem 0;

    &__title {
      margin-bottom: 42px;

      h2 {
        text-align: center;
        font-size: 28px;
        font-weight: 300;
        color: $gray-color3;
        margin-bottom: 20px;
        position: relative;

        &::after {
          content: "";
          display: inline-block;
          width: 40px;
          height: 4px;
          position: absolute;
          background: $main-color;
          top: 35px;
          left: 50%;
          transform: translateX(-50%);
