              color: red;
            }
          }

          input[type="text"] {
            border: none;
            font-size: 16px;
            padding-top: 4px;
            vertical-align: middle;
          }
        }

        input[type="submit"] {
          font-size: 16px;
          border: none;
          @extend %main-gradient;
          color: #fff;
          padding: 8px 42px;
          border-radius: 25px;
          display: block;
          margin: 1rem auto 0;
          cursor: pointer;

          &:hover {
            filter: brightness(1.1);
          }
        }
      }
    }
  }

  @include big {
    .contact--wrap {
      width: 90%;
      margin: 0 auto;
    }
  }
}
